.card{
  margin: 30px;
  display: grid;
  grid-template-columns: 300px;
  grid-template-rows: 210px 210px 80px;
  grid-template-areas: "image" "text" "stats";

  font-family:  roboto;
  border-radius: 18px;
  background: white;
  /* box-shadow:  5px 5px 115px rgb(0, 0,0); */
  box-shadow: 2px 2px 40px rgb(0, 0,0);
  text-align: center;

  -webkit-transition: 0.
  5s ease;

  transition: 0.
  5s ease;
  cursor: pointer;
}

.card-image{
  grid-area :image;
  background: url('https://66.media.tumblr.com/ed1b05038f8638d87cbf9a97f6307c3b/tumblr_nvyvq6GwAr1u7awiro1_1280.jpg');
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  background-size: cover;

}
.card-text{
  grid-area : text ;
  margin: 25px;
}
.card-text .date{
  color: rgb(255, 7,110);
  font-size: 13px;
}

.card-stats h2{
   margin-top: 0px;
   font-size: 28px;
  
  }
.card-stats{
grid-area: stats;
display: grid;
grid-template-columns: 1fr 1fr 1fr ;
grid-template-rows: 1fr;
border-bottom-left-radius: 15px;
border-bottom-right-radius: 15px;
background: rgb(255, 7,110);

}
.card-stats .stat{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 10x;
  color:white;

}
.card-stats .type{
  font-size: 12px;
  font-weight: 300;
  text-transform: uppercase;

}

.card-stats .value{
  font-size: 22px;
  font-weight: 500px;

}

